@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  scroll-behavior: smooth;
}

.App {
  min-height: 100vh;
  background-color: #39445a;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}

@media(max-width: 700px){
  .app{
    padding-top: 70px;
  }
}

.pageTitle{
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat",sans-serif;
  padding: 4px;
  border-radius: 50px;
  color: white;
  font-size: 2vw;
}

@media(max-width: 1000px){
  .pageTitle{
    font-size: 6.4vw;
  }
}

.header{
    background-color: #39445a;
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-family:"Montserrat", sans-serif;
    font-size: 5vw;
    padding-bottom: 15px;
    box-shadow: 0px 1px 5px black;
    color: white;
    z-index: 100;
}

@media (max-width: 1000px){
    .header{
        padding-top: 15px;
        font-size: 6.4vw;
    }
}
.content_landscape{
    object-fit: contain;
    border-radius: 10px;
}

.content_portrait{
    display: none;
    object-fit: contain;
    border-radius: 10px;
}
.tagline{
    padding-bottom: 10px;
    align-self: center;
}
.contentModal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
}
.contentModal::-webkit-scrollbar{
    display: none;
}
.contentModal_about{
    padding: 10px;
    width: 95%;
    height: 90%;
    display: flex;
    flex-direction: column;
    font-family: "Roboto",sans-serif;
    justify-content: space-evenly;
    font-weight: 300;
}
.contentModal_title{
    height: 12%;
    font-size: 5vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contentModal_description{
    display: flex;
    height: 40%;
    overflow-y: scroll;
    padding: 15px;
    border-radius: 20px;
    scrollbar-width: thin;
    box-shadow: inset 0 0 5px #000000;
}
.contentModal_description::-webkit-scrollbar{
    display: none;
}

@media(min-width: 835px){
    .content_landscape{
        display: none;
    }
    .content_portrait{
        display: flex;
        width: 38%;
    }
    .contentModal{
        flex-direction: row;
        justify-content: space-around;
        padding: 10px 0;
    }
    .contentModal_about{
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .contentModal_title{
        font-size: 3.5vw;
    }
    .contentModal_description{
        font-size: 20px;
    }
}
.carouselItem{
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
}

.carouselItem_img{
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
}
.media{
   display: flex;
   flex-direction: column;
   width: 200px;
   padding: 5px; 
   margin: 5px 0;
   background-color: #282c34;
   border-radius: 10px;
   position: relative;
   font-family: "Montserrat",sans-serif;
}

.media:hover{
    background-color: white;
    color: black;
}


@media(max-width: 550px){
    .media{
        width: 46%;
    }
    
}

.poster{
    border-radius: 10px;
}

.title{
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;;
}

.subtitle{
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
    padding-bottom: 3px;
}
.trending{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
